body {
    /* #7cc6fe */
    background-color:#8db1cc;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  * {
    font-family: "Verdana", cursive, sans-serif;
    color: #ffffff;
  }
  
  .app {
    /* #252d4a; */
    background-color: #19213c;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  
    width: 70%;
    min-width: 800px;
    min-height: 640px;
  
    border-radius: 15px;
    padding: 20px;
    margin: 32px auto;
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .welcome-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: fit-content;
    height: 640px;
    margin: 0 auto;
  }
  .welcome-bg {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-clip: text;
    background-image: url(./assets/quizbg.webp);
    width: 800px;
    height: 398px;
    background-position-x: 1000%;
  }
  
  @keyframes text_pop {
    from {
      scale: 0;
    }
    to {
      scale: 1;
    }
  }
  
  .welcome-text {
    padding: 0;
    margin: 0;
    scale: 0;
    animation: text_pop 1.6s ease 0.2s forwards;
    position: relative;
    font-family: "Anton", sans-serif;
    color: transparent;
    font-size: 350px;
    letter-spacing: -8px;
  }
  
  .quiz-container {
    min-height: 500px;
    display: flex;
    flex-direction: column;
  }
  .score-board {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 500px;
  }
  
  /* questions-options-container */
  .questions-options-menu {
    display: flex;
    justify-content: space-around;
    text-align: left;
    margin: 32px 0;
  }
  .questions-options-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    height: 640px;
  }
  
  .questions-categories-list {
    margin: 0 16px;
  }
  
  /* QUESTION/TIMER/LEFT SECTION */
  .question-section {
    display: flex;
    flex-direction: column;
  }
  
  .question-count {
    margin-bottom: 20px;
  }
  
  .question-count span {
    font-size: 28px;
  }
  .question-head {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .question-type {
    margin-bottom: 8px;
  }
  
  .question-difficulty {
    margin-bottom: 18px;
  }
  .question-text {
    max-width: 800px;
    text-align: center;
    margin-bottom: 12px;
    height: 80px;
  }
  
  .question-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    object-fit: contain;
  }
  
  .question-img {
    max-height: 300px;
    object-fit: cover;
  }
  .question-img-container {
    border: 1px solid whitesmoke;
    background: rgba(255, 255, 255, 1);
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .timer-text {
    text-align: center;
    width: 60px;
    margin: 0 auto;
    background: rgb(38, 59, 123);
    padding: 20px 20px 20px 20px;
    border: 4px solid whitesmoke;
    border-radius: 15px;
    position: absolute;
  }
  
  .alert-msg {
    margin-top: 16x;
    color: red;
    text-shadow: 1px 1px black;
  }
  
  
  .icon-category {
    width:22px;
    height:22px;
  }
  .icon-question-head {
    width:34px;
    height: 34px;
  }
  
  
  .answer-section {
    width: 80%;
    margin: 8px auto;
  }
  .checkbox-option {
    margin: 4px;
    display: flex;
    align-items: center;
  }
  .checkbox {
    transform: scale(1.8);
    margin: 8px;
  }
  .label {
    padding-left: 4px;
  }
  
  .btn {
    margin: 16px auto;
    padding: 5px;
    width: 42%;
    min-width: 300px;
  
    font-size: 16px;
    color: #ffffff;
    background-color: #252d4a;
    border: 5px solid #234668;
    border-radius: 15px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    cursor: pointer;
  }
  
  .btn-reset {
    text-align: center;
    margin-top: 32px;
  }
  
  button:hover {
    background-color: #555e7d;
  }
  
  button:focus {
    outline: none;
  }
  
  button svg {
    margin-right: 5px;
  }
  
  details {
    border-radius: 15px;
    background: whitesmoke;
    margin: 12px auto;
  }
  .details-question {
    text-align: center;
    border: 1px solid white;
    border-radius: 14px;
    padding: 12px;
    background: #234668;
  }
  .details-question-text {
    display: flex;
    flex-direction: column;
  }
  
  .summary {
    padding: 8px;
    color: #234668;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    margin-bottom: 4px;
  }
  .summary:hover {
    cursor: pointer;
  }
  
  details > summary::after,
  details > summary::before {
    content: "+";
    margin: 0 4px;
  }
  
  details[open] > summary::after,
  details[open] > summary::before {
    content: "-";
    margin: 0 4px;
  }
  
  details > summary::-webkit-details-marker {
    display: none;
  }
  
  .star-container {
    display: flex;
  }
  .star-img {
    width: 16px;
    margin: 2px;
  }
  
  .true-answer,
  .correct-answer {
    padding-top: 8px;
  }
  .score-details-container {
    text-align: center;
  }
  .score-points-text {
    text-align: center;
  }
  
  @keyframes sweep {
    0% {
      opacity: 0;
      margin-top: -10px;
    }
    100% {
      opacity: 1;
      margin-top: 0px;
    }
  }
  details[open] summary ~ * {
    animation: sweep 0.6s ease-in-out;
  }
  
  @media screen and (max-width: 900px) {
    @keyframes text_translate_Y {
      from {
        top: -130px;
      }
      to {
        top: 100px;
      }
    }
  
    .app {
      min-height: 700px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
  
      min-width: 450px;
      margin: 42px auto 0;
    }
  
    .questions-options-menu {
      flex-direction: column;
    }
    .question-img-container {
      max-height: 200px;
    }
    .question-img {
      height: 200px;
    }
  
    .btn {
      min-width: 320px;
    }
  
    .welcome-bg {
      margin: 0 auto;
      width: 380px;
      background-position: 0;
    }
    .welcome-text {
      top: -130px;
      scale: 1;
      animation: none;
      animation: text_translate_Y 2s ease 0.2s forwards;
      letter-spacing: -8px;
      font-size: 180px;
    }
  }
  
  @media screen and (max-width: 510px) {
    .app {
      min-height: 780px;
      width: 100%;
      min-width: 340px;
      padding: 0;
      margin: 0 0;
      border: none;
      border-radius: 0;
    }
    .question-img {
      max-width: 300px;
    }
    .welcome-bg {
      width: 100%;
    }
    .welcome-text {
      letter-spacing: -2px;
      font-size: 150px;
    }
  
    .answeroption-btn {
      width: 300px;
    }
  }
  